<template>
	<view>
		<uni-card :title="user.name" :sub-title="user.bio" extra="" thumbnail="https://qiniu-web-assets.dcloud.net.cn/unidoc/zh/unicloudlogo.png">
			<uni-grid :column="2" :show-border="true" :square="false" @change="onGridChange">
				<uni-grid-item :index="0">
					<view class="grid-item-content">
						<text class="grid-item-number">￥{{ user.balance }}</text>
						<text class="grid-item-text">余额</text>
					</view>
				</uni-grid-item>
				
				<uni-grid-item :index="1">
					<view class="grid-item-content">
						<text class="grid-item-number">{{ user.score }}</text>
						<text class="grid-item-text">积分</text>
					</view>
				</uni-grid-item>
			</uni-grid>
		</uni-card>



		<uni-list>
			<uni-list-item 
				title="订单列表" 
				:rightText="user.balance"
				:show-extra-icon="true" showArrow is-link
				:extra-icon="{size: '22', type: 'cart' }"
			>
			</uni-list-item>
			<uni-list-item
				title="账号管理" 
				:rightText="user.balance"
				:show-extra-icon="true" showArrow is-link
				:extra-icon="{ color: '#0d6efd', size: '22', type: 'gear' }"
			>
			</uni-list-item>
			<uni-list-item
				title="日志记录" 
				:rightText="user.balance"
				:show-extra-icon="true" showArrow is-link
				:extra-icon="{ color: '#0d6efd', size: '22', type: 'list' }"
			>
			</uni-list-item>
			<uni-list-item
				title="版本号" 
				rightText="V24.12.31"
				:show-extra-icon="true"
				:extra-icon="{ color: '#0d6efd', size: '22', type: 'info' }"
			>
			</uni-list-item>
		</uni-list>
	</view>
</template>

<script>
	import {mapActions} from 'pinia'
	import {userStore} from '@/stores/user'
	export default {
		data() {
			return {
				title: '个人中心',
				user: {}
			}
		},
		beforeCreate() {
			this.auth.check()
		},
		onLoad() {
			this.fetch()
			// uni.reLaunch({ url: '/pages/auth/login' })
		},
		onPullDownRefresh() {
			// 刷新用户信息
			this.fetch()
		},
		methods: {
			...mapActions(userStore, ['userInfo']),
			fetch() {
				this.userInfo().then(resp => {
					this.user = resp.data
					uni.stopPullDownRefresh()
				})
			}
		}
	}
</script>

<style>

</style>